:root {
  --brand-green: #166534;   /* primary */
  --brand-green-dark: #14532d;
  --brand-orange: #ea580c;  /* accent */
  --brand-bg: #faf7f2;
  --brand-card: #ffffff;
  --brand-border: #e5e7eb;
  --text-main: #1f2937;
  --text-muted: #6b7280;
}

/* ===== Global ===== */
body {
  background: var(--brand-bg);
  color: var(--text-main);
  font-family: "Segoe UI", system-ui, -apple-system, BlinkMacSystemFont, sans-serif;
}

h2, h3, h4 {
  font-weight: 600;
  letter-spacing: 0.2px;
}

button {
  font-weight: 600;
  transition: transform 0.05s ease, box-shadow 0.15s ease, background 0.2s;
}

button:active {
  transform: scale(0.98);
}
.header {
  padding: 12px 8px;
  border-bottom: 1px solid var(--brand-border);
}

.header h2 {
  color: var(--brand-green);
  font-weight: 700;
}
.card {
  background: var(--brand-card);
  border-radius: 14px;
  border: 1px solid var(--brand-border);
  box-shadow: 0 6px 16px rgba(0,0,0,0.06);
  transition: box-shadow 0.2s ease, transform 0.15s ease;
}

.card:hover {
  box-shadow: 0 10px 22px rgba(0,0,0,0.08);
  transform: translateY(-2px);
}
.button-orange {
  background: var(--brand-orange);
  color: #fff;
}

.button-orange:hover {
  background: #c2410c;
}

.button-green {
  background: var(--brand-green);
  color: #fff;
}

.button-green:hover {
  background: var(--brand-green-dark);
}
input, select {
  padding: 8px;
  border-radius: 8px;
  border: 1px solid var(--brand-border);
  font-size: 14px;
}

input:focus, select:focus {
  outline: none;
  border-color: var(--brand-green);
  box-shadow: 0 0 0 2px rgba(22,101,52,0.15);
}
.cart-bar {
  border-top: 1px solid var(--brand-border);
  background: #ffffffee;
  backdrop-filter: blur(4px);
}

/* ===== Layout ===== */
.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
}

/* ===== Cards ===== */
.card {
  background: #fff;
  padding: 12px;
  border-radius: 14px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.08);
}

.card strong {
  color: #7c2d12;
}

/* ===== Stock States ===== */
.low-stock {
  border: 2px solid #facc15;
  background: #fffbeb;
}

.out-stock {
  border: 2px solid #dc2626;
  background: #fef2f2;
}

/* ===== Buttons ===== */
.btn-primary {
  background: #d9480f;
  color: #fff;
  padding: 10px;
  border-radius: 10px;
  border: none;
  width: 100%;
}

.btn-primary:disabled {
  background: #fca5a5;
}

.btn-confirm {
  background: #2e7d32;
  color: #fff;
  padding: 12px;
  border-radius: 12px;
  border: none;
  width: 100%;
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
}

.header-left {
  display: flex;
  align-items: center;
  gap: 10px;
}

.header-logo {
  width: 42px;
  height: 42px;
  border-radius: 8px;
}

.header-title {
  display: flex;
  flex-direction: column;
}

.header-title span {
  font-size: 12px;
  color: var(--text-muted);
}
.section-title {
  margin-top: 18px;
  margin-bottom: 8px;
  font-size: 18px;
  font-weight: 700;
  color: var(--brand-green);
  border-bottom: 2px solid #e5e7eb;
  padding-bottom: 4px;
}
.price {
  font-size: 16px;
  font-weight: 700;
  margin: 6px 0;
  color: #111827;
}
.search-box {
  position: sticky;
  top: 0;
  z-index: 10;
  background: var(--brand-bg);
  padding-bottom: 8px;
}
.product-card {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.product-name {
  font-size: 15px;
  font-weight: 700;
  color: #111827;
}

.product-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product-weight {
  font-size: 13px;
  color: #6b7280;
}

.product-price {
  font-size: 16px;
  font-weight: 700;
}
.add-btn {
  margin-top: 6px;
  padding: 12px;
  font-size: 15px;
  font-weight: 600;
}
.badge {
  font-size: 11px;
  padding: 2px 6px;
  border-radius: 6px;
  margin-left: 6px;
  font-weight: 600;
}

.badge-low {
  background: #fef3c7;
  color: #92400e;
}

.badge-out {
  background: #fee2e2;
  color: #991b1b;
}
.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  font-size: 14px;
}

.cart-item-left {
  flex: 1;
}

.qty-controls {
  display: flex;
  align-items: center;
  gap: 6px;
}

.qty-btn {
  width: 28px;
  height: 28px;
  border-radius: 6px;
  border: 1px solid #ccc;
  background: #fff;
  font-weight: bold;
}
.cart-total {
  font-size: 18px;
  font-weight: 700;
  margin-top: 6px;
}

/* ===== Cart Bar ===== */

/* ===== Admin ===== */
.admin-box {
  background: #fff;
  padding: 14px;
  border-radius: 14px;
  margin-bottom: 14px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.06);
}

.admin-tabs button {
  flex: 1;
  padding: 8px;
  border-radius: 10px;
  border: none;
  background: #fde68a;
  color: #92400e;
}

.admin-tabs button.active {
  background: #d9480f;
  color: #fff;
}

/* ===== Mobile ===== */
@media (max-width: 600px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
/* ===== HEADER ===== */
.vh-header {
  position: sticky;
  top: 0;
  z-index: 100;
  background: #fff7ed;
  padding: 12px;
  border-bottom: 2px solid #f97316;
}

.vh-brand {
  display: flex;
  align-items: center;
  gap: 10px;
}

.vh-logo {
  width: 48px;
  height: 48px;
}

.vh-title {
  margin: 0;
  color: #7c2d12;
  font-size: 20px;
}

.vh-tagline {
  font-size: 12px;
  color: #92400e;
}

.vh-header-actions button {
  background: #fde68a;
  border: none;
  padding: 6px 10px;
  border-radius: 8px;
  margin-left: 6px;
}
